main {
    min-height: 100vh;
  }
  
  

  
  .main-header {
    
      display: flex;
      justify-content: flex-start;
     
  }
  
  
  .burger-style {
   
      width: 5vh;
      height: 3.5vh;
      margin-left: 3vh;
      padding-top: 3.5vh;
      cursor: pointer;
  }

.burgerKors {
    width: 5vh;
    height: 3.5vh;
    margin-left: 3vh;
    padding-top: 3.5vh;
    cursor: pointer;
    filter: invert(12%) sepia(100%) saturate(5100%) hue-rotate(190deg) brightness(86%) contrast(88%);
}

.burgerTea {
    width: 5vh;
    height: 3.5vh;
    margin-left: 3vh;
    padding-top: 3.5vh;
    cursor: pointer;
    filter: invert(12%) sepia(100%) saturate(5432%) hue-rotate(353deg) brightness(86%) contrast(88%);
}
  
  .main-footer {
    margin-left:25px;
    margin-bottom:25px ;
    display: flex;
    font-size: 20pt;
  }
  .main-footer a{
    margin-top: 5px;
    margin-left: 10px;
    color: #00984f;
    text-decoration: none;
  }
  .main-footer img {
    
      cursor: pointer;
  }
 
  .avatar {
      margin-top: 2.25vh;
      margin-left: auto;
      margin-right: 5vh;
      font-size: 3vh;
      width: 6vh;
      height: 6vh;
      border-radius: 50%;
      background: #555;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
    
    .avatar::after {
      content: attr(data-label);
      font-family: "bionbook_cond";
      color: #ffffff;
    }
   

.logoHeader img{
    max-width: 10vh;
    cursor: pointer;
  
  }

  .main-header img:hover {
    opacity: 0.6;
    transition: opacity 350ms;
  }
  .main-header img {
    opacity: 1;
    transition: opacity 350ms;
  }

  @media (min-width: 250px) and (max-width: 500px) {
    .burger-style{
        margin-left:1vh ;
    }
  }